/*
 * The main dark mode styles
 */

 @mixin dark-scheme {
    /* framework */
    --main-wrapper-bg: #36393F;
    --body-bg: var(--main-wrapper-bg);
    --topbar-wrapper-bg: rgb(39, 40, 43);
    --search-wrapper-bg: rgb(34, 34, 39);
    --search-icon-color: rgb(100, 102, 105);
    --input-focus-border-color: rgb(112, 114, 115);
    --mask-bg: rgb(68, 69, 70);
    --footer-bg-color: var(--main-wrapper-bg);
  
    /* common color */
    --text-color: rgb(235, 235, 235);
    --heading-color: rgb(255, 255, 255);
    --text-muted-color: rgb(187, 196, 204);
    --link-color: rgb(138, 180, 248);
    --link-underline-color: rgb(82, 108, 150);
    --main-border-color: rgb(44, 45, 45);
    --button-bg: rgb(39, 40, 43);
    --blockquote-border-color: rgb(66, 66, 66);
    --blockquote-text-color: rgb(117, 117, 117);
    --btn-border-color: rgb(63, 65, 68);
    --btn-backtotop-color: var(--text-color);
    --btn-backtotop-border-color: var(--btn-border-color);
    --btn-box-shadow: var(--main-wrapper-bg);
    --card-header-bg: rgb(51, 50, 50);
    --label-color: rgb(168, 177, 185);
    --checkbox-color: rgb(118 120 121);
    --checkbox-checked-color: var(--link-color);
  
    /* Sidebar */
    --nav-cursor-color: rgb(183, 182, 182);
    --sidebar-bg: radial-gradient(circle, #2F3136 0%, #202225 100%);
  
    /* Top Bar */
    --topbar-text-color: var(--text-color);
  
    /* Home page */
    --post-list-text-color: rgb(175, 176, 177);
    --btn-patinator-text-color: var(--text-color);
    --btn-paginator-hover-color: rgb(64, 65, 66);
    --btn-active-bg: rgba(28, 52, 94, 1);
    --btn-active-border-color: rgb(66, 94, 138);
    --btn-text-color: var(--text-color);
    --btn-paginator-border-color: var(--btn-border-color);
    --btn-paginator-shadow: var(--main-wrapper-bg);
    --pin-bg: rgb(34 35 37);
    --pin-color: inherit;
  
    /* Posts */
    --toc-highlight: rgb(116, 178, 243);
    --tag-bg: rgb(41, 40, 40);
    --tag-hover: rgb(43, 56, 62);
    --tb-odd-bg: rgba(42, 47, 53, 0.52); /* odd rows of the posts' table */
    --tb-even-bg: rgb(31, 31, 34); /* even rows of the posts' table */
    --tb-border-color: var(--tb-odd-bg);
    --footnote-target-bg: rgb(63, 81, 181);
    --btn-share-color: #6c757d;
    --btn-share-hover-color: #bfc1ca;
    --relate-post-date: var(--text-muted-color);
    --card-bg: rgb(39, 40, 43);
    --card-border-color: rgb(53, 53, 60);
    --card-box-shadow: var(--main-wrapper-bg);
  
    /* tags */
    --tag-border: rgb(59, 79, 88);
    --tag-shadow: rgb(32, 33, 33);
    --search-tag-bg: var(--tag-bg);
    --dash-color: rgb(63, 65, 68);
  
    /* categories */
    --categories-border: rgb(64, 66, 69);
    --categories-hover-bg: rgb(73, 75, 76);
  
    /* archives */
    --timeline-node-bg: rgb(150, 152, 156);
    --timeline-color: rgb(63, 65, 68);
    --timeline-year-dot-color: var(--timeline-color);
  
    /* Footer */
    --footer-link: rgb(171, 171, 171);
  
    .post-content img {
      filter: brightness(90%);
    }
  
    hr {
      border-color: var(--main-border-color);
    }
  
    /* posts' toc, override BS */
    nav[data-toggle=toc] .nav-link.active,
    nav[data-toggle=toc] .nav-link.active:focus,
    nav[data-toggle=toc] .nav-link.active:hover,
    nav[data-toggle=toc] .nav > li > a:focus,
    nav[data-toggle=toc] .nav > li > a:hover {
      color: var(--toc-highlight) !important;
      border-left-color: var(--toc-highlight) !important;
    }
  
    /* categories */
    .categories.card,
    .list-group-item {
      background-color: var(--card-bg);
    }
  
    .categories {
      .card-header {
        background-color: var(--card-header-bg);
      }
      .list-group-item {
        border-left: none;
        border-right: none;
        padding-left: 2rem;
        border-color: var(--categories-border);
        &:last-child {
          border-bottom-color: var(--card-bg);
        }
      }
    }
  
    #archives li:nth-child(odd) {
      background-image: linear-gradient(
        to left,
        rgb(26, 26, 30),
        rgb(39, 39, 45),
        rgb(39, 39, 45),
        rgb(39, 39, 45),
        rgb(26, 26, 30));
    }

    --selection-color: #0B68D9;

    ::selection {
      background: var(--selection-color);
      color: var(--text-color);
    }

    ::-moz-selection {
      background: var(--selection-color);
      color: var(--text-color);
    }

    .text-muted {
      color: var(--text-muted-color) !important;
    }

    .nav-link {
      color: var(--text-muted-color) !important;
    }
    
    .blockquote-definition {
      color: #204922;
      background-color: #8BE4A2;
      border-color: #7dcd92
    }

    .blockquote-theorem {
      color: #024666;
      background-color: #77ebdf;
      border-color: #6bd4c9;
    }

    .blockquote-example {
      color: #383d41;
      background-color: #cbccce;
      border-color: #c1c2c5;
    }

    blockquote-example-no-text {
      color: #383d41;
      background-color: #cbccce;
      border-color: #c1c2c5;
    }

    .blockquote-warning {
      color: #5e482a;
      background-color: #EAB569;
      border-color: #bb9054;
    }

    .blockquote-note {
      color: #130d19;
      background-color: #BB86F6;
      border-color: #956bc5;
    }
  } // dark-scheme